<style scoped>
</style>


<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item v-show="display.home" :to="{ path: '/home' }">首页</el-breadcrumb-item>

      <el-breadcrumb-item v-show="display.repo_net_list" :to="{path:'/reponet'}">云仓库</el-breadcrumb-item>
      <el-breadcrumb-item v-show="display.repo_net_detail" :to="{path:'/'}">云仓库详情</el-breadcrumb-item>

      <el-breadcrumb-item v-show="display.repository_list" :to="{path:'/repository'}"> 本地仓库</el-breadcrumb-item>
      <el-breadcrumb-item v-show="display.repository_detail" :to="{path:'/'}">本地库详情</el-breadcrumb-item>

      <el-breadcrumb-item v-show="display.user_device_list" :to="{path:'/device'}">设备</el-breadcrumb-item>
      <el-breadcrumb-item v-show="display.user_device_detail" :to="{path:'/'}">设备详情</el-breadcrumb-item>

      <el-breadcrumb-item v-show="display.download_client" :to="{path:'/'}">下载客户端</el-breadcrumb-item>

      <el-breadcrumb-item>{{path_title}}</el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>


<script>
export default {
  name: "PathBar",

  props: ["value"],

  model: {
    prop: "value",
    event: "balabala"
  },

  data() {
    return {
      path_elements: ["a", "b"],
      path_title: "xxx",

      display: {
        download_client: false,
        home: true,
        user_device_detail: false,
        user_device_table: true
      }
    };
  },

  mounted() {
    var elements = this.value.path;
    this.path_elements = elements;
    this.path_title = this.value.title;
    var disp = {};
    for (var i in elements) {
      var name = elements[i];
      disp[name] = true;
    }
    this.display = disp;
  },

  methods: {}
};
</script>
