
<style scoped>
.dialog {
  max-width: 500px;
  margin-left: auto;
  margin-right: auto;
}
</style>

<template>
  <el-dialog
    title="新建云仓库"
    :visible.sync="dialogVisible"
    width="90%"
    class="dialog"
    :before-close="handleClose"
  >
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="标签">
        <el-input v-model="form.label"></el-input>
      </el-form-item>
      <el-form-item label="备注">
        <el-input type="textarea" v-model="form.description"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">取 消</el-button>
      <el-button type="primary" @click="onClickOK">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: "RepoNetInsertDialog",

  data() {
    return {
      form: {
        name: "",
        label: "",
        description: ""
      },
      dialogVisible: false
    };
  },

  computed: {},

  mounted() {},

  methods: {
    onClickOK() {
      this.doInsert();
    },

    doInsert() {
      var self = this;

      var description = this.form.description;
      var label = this.form.label;
      var name = this.form.name;

      var id = null;
      var type = "RepoNet";
      var data = {
        net: {
          entity: { name, label, description }
        }
      };

      var on_ok = res => {
        self.onSuccess();
      };

      var on_error = res => {
        self.onError(res);
      };

      this.$store.dispatch("rest/post", { id, type, data, on_ok, on_error });
    },

    onSuccess() {
      this.dialogVisible = false;
      this.$store.dispatch("RepoNet/refresh", {});
    },

    onError(res) {},

    showDialog() {
      this.form.name = "";
      this.form.label = "";
      this.form.description = "";
      this.dialogVisible = true;
    },

    handleClose() {
      // this.dialogVisible = false;
    }
  }
};
</script>
